body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }
  
/* overall */
* {
  list-style-type: none;
  padding: 0;
  margin: 0;
  color: rgb(255, 255, 255);
  text-decoration: none;
}

body {
  -ms-overflow-style: none;
  min-height: 700px;
}

::-webkit-scrollbar {
  display: none;

}

/* Navigation.js */
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.navi_img{
  margin-left: 20px;
  height: 50px;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(8px + 2vmin);
  color: white;
  border-bottom: #fff 1px;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* home.js */
.home{
  height: 10000;
}
.backgroundImage {
  height: 100vh;
  width: 100vw;
  object-fit: cover;
  opacity: 50%;
}
#one .schoolLogo{
  position: absolute;
  top: 20vh;
  right: 47vw;
  width: 80px;
  background-color: #ffffff00;
}
#one h1{
  font-size:5.5vw;
  line-height: 7vw;
  position: absolute;
  top: 30vh;
  right:25vw;
  background-color: #ffffff00;
}
#one h1 p{
  position: absolute;
  right: 15vw;
  background-color: #ffffff00;
}


/* home.js section3 */
#three {
  justify-content: column;
}

#section3_title {
  font-size: 50px;
  font-weight: bold;
  color: white;
  text-align: center;
  margin: 70px 0 70px 0;
}

#section3_content {
  overflow: scroll;
  display: flex;
  flex-direction: row;
  align-items: left;
  flex-wrap: nowrap;
  margin-bottom: 10px;
}

.item { 
  width: 300px;
  height: 225px;
  padding: 15px;
  flex-grow: 0;
  flex-shrink: 0;
  margin: 0 25px 0 25px;
  background-image: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ),url('https://media.euobserver.com/e0e217ad29a2887c28b3732eeef3ee98.jpg');
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  transition: 0.3s;
}

.item h2 {
  background: none;
  text-align: center;
  margin: inherit;
  padding: inherit;
  font-size: 30px;
}



.item:hover {
  box-shadow: 5px 10px 5px #fff;
}

@media screen and (max-width: 1000px) {
  .item {
    width: 200px;
    height: 150px;
  }

  .item h2 {
    font-size: 20px;
  }

}

/* home.js section4 */
#section4_content {
  width: 100%;
}

#box1 {
  width: 50%;
  height: 800px;
  float: left;
  background-image: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ),url('https://media.euobserver.com/e0e217ad29a2887c28b3732eeef3ee98.jpg');
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  display: flex;
  opacity: 0.5;
  transition: 0.3s;
}

#box2, #box3 {
  width: 50%;
  height: 400px;
  float: right;
  background-image: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ),url('https://media.euobserver.com/e0e217ad29a2887c28b3732eeef3ee98.jpg');
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  display: flex;
  opacity: 0.5;
  transition: 0.3s;
}

.boxtext {
  background: none;
  margin: auto;
  padding: inherit;
}

#box1:hover, #box2:hover, #box3:hover {
  opacity: 1;
}

/* student.js */
.banner img {
  width: 100%;
  max-height: 100px;
  margin: 20px 0;
  object-fit: cover;
  opacity: 50%;
}

.banner h2 {
  position: relative;
  top: -90px;
  z-index: 3;
  background-color: #ffffff00;
}

.trinity {
  width: 330px;
  height: 330px;
  border: 1px solid #CCC;
  position: relative;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  border-radius: 50%;
  margin-bottom: 10%;
}
.trinity h3 {
  position: relative;
  top: 45%;
  background-color:#ffffff00;
}

.student {
  width: 100px;
  height: 100px;
  background: rgb(255, 255, 255);
  border-radius: 50%;
  position:absolute;
  overflow: hidden;
}
.student img{
  height: 40px;
  width: 40px;
  margin-top: 7px;
  background-color:white;
}
.student h6 {
  position: absolute;
  top: 50%;
  background-color: #ffffff00;
  color: black;
}

#council {
  bottom: 0;
}
#congress {
  -webkit-transform: rotate(200deg) translate(-290px) rotate(-200deg);
          transform: rotate(200deg) translate(-290px) rotate(-200deg);
}
#court {
  -webkit-transform: rotate(135deg) translate(-100px) rotate(-135deg);
          transform: rotate(135deg) translate(-100px) rotate(-135deg); 
}

.circle {
  width: 30px;
  height: 30px;
  background: rgb(255, 255, 255);
  border-radius: 50%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  margin: auto;
  -webkit-animation: circle 6s linear infinite;
          animation: circle 6s linear infinite;
}


@-webkit-keyframes circle {
  0% {
    -webkit-transform: rotate(0deg) translate(-165px) rotate(0deg);
            transform: rotate(0deg) translate(-165px) rotate(0deg);

  }

  100% {
    -webkit-transform: rotate(360deg) translate(-165px) rotate(-360deg);
            transform: rotate(360deg) translate(-165px) rotate(-360deg);
  }
}


@keyframes circle {
  0% {
    -webkit-transform: rotate(0deg) translate(-165px) rotate(0deg);
            transform: rotate(0deg) translate(-165px) rotate(0deg);

  }

  100% {
    -webkit-transform: rotate(360deg) translate(-165px) rotate(-360deg);
            transform: rotate(360deg) translate(-165px) rotate(-360deg);
  }
}
@media screen and (max-width: 400px) {
  .trinity {
    width: 250px;
    height: 250px;
  }
  .student {
    width: 80px;
    height: 80px;
  }
  #congress {
    -webkit-transform: rotate(200deg) translate(-220px) rotate(-200deg);
            transform: rotate(200deg) translate(-220px) rotate(-200deg);
  }
  .student img{
    height: 30px;
    width: 30px;
  }
  .student h6{
    font-size: 13px;
  }
  @-webkit-keyframes circle {
    0% {
      -webkit-transform: rotate(0deg) translate(-125px) rotate(0deg);
              transform: rotate(0deg) translate(-125px) rotate(0deg);
  
    }
  
    100% {
      -webkit-transform: rotate(360deg) translate(-125px) rotate(-360deg);
              transform: rotate(360deg) translate(-125px) rotate(-360deg);
    }
  }
  @keyframes circle {
    0% {
      -webkit-transform: rotate(0deg) translate(-125px) rotate(0deg);
              transform: rotate(0deg) translate(-125px) rotate(0deg);
  
    }
  
    100% {
      -webkit-transform: rotate(360deg) translate(-125px) rotate(-360deg);
              transform: rotate(360deg) translate(-125px) rotate(-360deg);
    }
  }
}
.popup {
  position: fixed;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 600px;
  height: 600px;
  box-shadow: 0px 10px 20px 0px #7e8284;
  border-radius: 10px;
  background-color: white;
  visibility: hidden;
  opacity: 0;
  transition: opacity 500ms;
  z-index: 3;
}

.popup:target {
  visibility: visible;
  opacity: 1;
}

.popup a {
  text-decoration: none;
  font-size: 20px;
  color: black;
  background-color: white;
  transition: opacity 500ms;
  position: absolute; 
  right: 10px;
}

.popup p {
  color: black;
  text-align: left;
  background-color: white;
  font-size: 16px;
  display: block;
  word-wrap: break-word;
  margin: 30px 20px 0px 20px;
}

